/* navbar */

.main-nav{
  background-color: rgb(46, 44, 44);
  position: sticky;
}
.inner-nav{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo{
  margin-top: 10px;
  margin-left: 10px;
  color: white;
}

/* register page and login page css */
.register-form-heading,.login-form-heading, .edit-form-heading{
  text-align: center;
  margin-top: 8%;
}
.add-product-form-heading{
  text-align: center;
  margin-top: 3%;
}

#register-main, #login-main{
  border: 1px solid rgb(197, 195, 195);
  max-width: 500px;
  width: 80%;
  margin: auto;
  padding: 20px;
  margin-top: 2%;
  border-radius: 12px;
}
#register-btn{
  background-color:aquamarine;
  margin: auto;
}

#login-btn{
  background-color:aquamarine;
  margin: auto;
  margin-top: 10px;
}
#new-user-btn{

  background-color:rgb(240, 235, 235);
  margin: auto;
  margin-top: 20px;
}


/* Navbar css */
.main-nav{
  max-width: 1650px;
  width: 100%;
  border: 1px solid black;
  height: 60px;
  margin: auto;
}


/* home css */
.container-m{
  background-color: rgb(46, 44, 44);

}
.home-main-wrapper{
  max-width: 1650px;
  width: 100%;
  margin: auto;
  display: flex;
}
.home-aside{
  height:100vh;
  width: 20%;
  background-color: rgb(46, 44, 44);
  color: azure;
}
.home-aside h3{
  text-align: center;
  margin-top: 10px;
}
.home-aside-top-div{
  width: 100%;
}
.home-aside input,select{
  background-color: rgb(168, 172, 172);
}
.product-filter-sort{
 width: 100%;
 margin: auto;
}
.filter-div{
  display: flex;
  flex-direction: column;
  margin:20px;
}
.home-main-container{
  width: 80%;
}
.h-top-bar{
  width: 100%;
  background-color: rgb(70, 69, 69);
  color: azure;
}
.dp-mp{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#mp{
  margin-right: 20px;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 10px;
  border-radius: 12px;
  margin: 10px;

}
.dp{
  margin-left: 10px;
}


.card-wrapper{
  display: flex;
  justify-content: space-around;
  background-color: rgb(92, 89, 89);
  flex-wrap: wrap;
  padding: 10px;
}

/* card component */
.card{
  max-width: 300px;
  width: 300px;
  min-width: 150px;
  margin: 10px;
  background-color: rgb(214, 212, 212);
}
.card p{
  color: white;
}


.img-responsive-21x9 {
  --tblr-img-responsive-ratio: 70.8571428571%;
  object-fit: cover;
}

.yp{
  background-color: rgb(92, 89, 89);
  font-size: 22px;
  font-weight: bold;
  padding: 10px;
}
.yp-inner{
  height: auto;
  background-color: rgb(92, 89, 89);
  font-size: 18ox;
  font-weight: bold;
  padding: 10px;
  margin-top: 10px;
  /* border: 1px solid red; */

}

.add-product-form #login-main{
  border: 1px solid black;
}
#mbb-6{
  margin-top: -40px;
}

#rbn{
  border: 1px solid black;
  margin-right: 45vw;
  margin-top: 45vh;
}
#rbn button{
  border: none;
  margin: 10px;
  background-color: inherit;
  color: white;
  font-size: 20px;
}
#rbn button:hover #sp-2{
           display: block;
}
#rbn button:hover #sp-1{
  display: none;
}
#sp-2{
  display: none;
}
.loading-l{
  margin-top: 20%;
  margin-left: 20%;
  margin-right: 20%;
  width: auto;
}


.not-found-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
  background-color: #f0f0f0;
  color: #333;
}

.not-found-content {
  margin-bottom: 20px;
}

.not-found-content h1 {
  font-size: 10rem;
  margin: 0;
  color: #ff6b6b;
}

.not-found-content h2 {
  font-size: 2.5rem;
  margin: 10px 0;
}

.not-found-content p {
  font-size: 1.2rem;
  margin: 10px 0 20px;
}

.home-link {
  padding: 10px 20px;
  font-size: 1rem;
  color: white;
  background-color: #007bff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.home-link:hover {
  background-color: #0056b3;
}

.not-found-illustration img {
  width: 100%;
  max-width: 400px;
  height: auto;
}

#show-error{
  color: red;
}

@media (max-width:820px) {
  .card-wrapper{
    display: flex;
    flex-wrap: wrap;
  }
 
}

@media (max-width:620px) {
  .card-wrapper{
    display: flex;
    flex-wrap: wrap;
  }
 
}